import {
  useState,
  useRef
} from "react";
import "./App.css";
import * as math from 'mathjs';

function App() {
  const inputRef = useRef(null);
  const resultRef = useRef(null);
  const [result, setResult] = useState(0);
  const val = math.evaluate(!inputRef.current.value ? 0 : inputRef.current.value);
  // inputRef.current = !inputRef.current? 0 : inputRef.current;
  function plus(e) {
    e.preventDefault();
    setResult((result) => result =
      !result ?
        val :
        result + val)
  };

  function minus(e) {
    e.preventDefault();
    setResult((result) => result =
      !String(result).length ?
        0 - val :
        result - val)
  };

  function times(e) {
    e.preventDefault();
    setResult((result) => result =
      !result ?
        0 :
        result * val);
  };
  
  function divide(e) {
    e.preventDefault();
    Number(inputRef.current.value) === 0 ?
      alert("ERROR: Cannot divide by ZERO!") :
      setResult((result) => result =
        !result ?
          0 / val :
          result / val)
  };

  function squareRoot(e) {
    e.preventDefault();
    Number(inputRef.current.value) < 0 ?
      alert("ERROR: Cannot calculate a square root of a negative number!") :
      setResult((result) => result = Math.sqrt(val));
  };

  function percentage(e) {
    e.preventDefault();
    Number(inputRef.current.value) < 0 ?
      alert("ERROR: Cannot calculate a negative percentage of a number!") :
      setResult((result) => !result ?
        (val / 100) * 0 :
        (val / 100)* result
      )
  };
  

  function calculate(e) {
    e.preventDefault();
    setResult(result => result = val);
  };
  

  function resetInput(e) {
    e.preventDefault();
    inputRef.current.value = "";
  };

  function resetResult(e) {
    e.preventDefault();
    setResult(0);
  };
  
  return (
    <div className="App">
      <div>
        <h1>Simplest Working Calculator</h1>
      </div>
      <form>
        <p
        // ref={resultRef}// DON KNOW WHY IT'S EVEN WRITTEN HERE
        >
          {result}
        </p>
        <input
          pattern=""
          ref={inputRef}
          type={"text"}
          placeholder="Type a number"
        />

        <button onClick={calculate}>Calculate</button>
        <button onClick={plus}>Plus</button>
        <button onClick={minus}>Substract</button>
        <button onClick={times}>Multiply</button>
        <button onClick={divide}>Divide</button>
        <button onClick={squareRoot}>Square Root</button>
        <button onClick={percentage}>Percentage</button>
        <button onClick={resetInput}>Reset Input</button>
        <button onClick={resetResult}>Reset Result</button>
      </form>
    </div>
  );
}

export default App; 
